<template>
  <pop-up
    title="Selectie importeren"
    @close="$emit('close')"
  >
    <form class="combine-popup__form" @submit.prevent="onSubmit">
      <fieldset>
        <div
          class="control-group"
          :class="{ 'has-error': showError }"
        >
          <label class="control-label" for="url">Link</label>
          <div class="controls url-box">
            <input
              class="block"
              id="url"
              type="text"
              name="url"
              v-model="url"
            >
            <em v-if="showError" class="errortext">{{ errorText }}</em>
          </div>

          <p>U kunt hier een LIWO-Link invoeren die u eerder heeft opgevraagd of die u van iemand hebt ontvangen. Door op importeren te klikken worden de scenario's aan uw bestaande selectie toegevoegd.</p>
        </div>

        <footer class="control-group combine-popup__footer">
          <div class="controls">
            <button
              class="btn primary"
            >
              Importeren
            </button>
            <button
              type="button"
              class="btn secondary"
              @click="$emit('close')"
            >
              Annuleer
            </button>
          </div>
        </footer>
      </fieldset>
    </form>
  </pop-up>
</template>

<script>
import PopUp from './PopUp'

export default {
  components: {
    PopUp
  },
  data () {
    return {
      url: '',
      showError: false,
      errorText: 'Geen valide URL'
    }
  },
  methods: {
    onSubmit () {
      let re = /(\d+)(,\d+)*$/
      let match = this.url.match(re)
      if (!match) {
        this.showError = true
      } else {
        this.$emit('close')
        // go to the new page
        this.$router.push({ params: { ids: match[0] } })
      }
    }
  },
  watch: {
    url (newValue, oldValue) {
      if (newValue !== oldValue) {
        this.showError = false
      }
    }
  }
}
</script>

<style>
  .combine-popup__form {
    margin-bottom: 0;
  }

  .combine-popup__footer {
    margin-bottom: 0;
  }

  .combine-popup__footer .btn {
    margin-right: 10px;
  }

  .combine-popup__form .url-box {
    margin-bottom: 1rem;
  }
</style>
